#menu {
  position: relative;
  z-index: 3;
  width: 100%;
}

#menu.menu-full-width {
  background: var(--color);
}

#menu .menu {
  display: flex;
  background: var(--color);
  border-radius: 3px;
}

.menu .menu-item {
  position: relative;
}

.menu .menu-item:first-child {
  margin-left: 0;
}

.menu .menu-item a {
  display: inline-block;
  color: #fff;
  border: 0;
  font-size: 0.8rem;
  font-weight: 500;
  padding: 1rem 2rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  border-left: 1px solid rgba(255, 255, 255, 0.2);
  position: relative;
  z-index: 1;
}

.menu .menu-item::after,
.menu .menu-item::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  opacity: 0.15;
}

.menu .menu-item:first-child,
.menu .menu-item:first-child::after,
.menu .menu-item:first-child::before {
  border-radius: 3px 0 0 3px;
}

.menu .menu-item:last-child,
.menu .menu-item:last-child::after,
.menu .menu-item:last-child::before {
  border-radius: 0 3px 3px 0;
}

.menu .menu-item:hover::before {
  background: #fff;
}

.menu .current-menu-item::after,
.menu .current-menu-parent::after,
.menu .current-menu-ancestor::after {
  background: #000;
}

.menu .current-menu-item::before,
.menu .current-menu-parent::before,
.menu .current-menu-ancestor::before {
  display: none;
}

#menu .menu .current-menu-item a::after,
#menu .menu .current-menu-parent a::after,
#menu .menu .current-post-ancestor a::after {
  content: "";
  position: absolute;
  bottom: -10px;
  right: 1rem;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 10px 20px 0 5px;
  border-color: var(--color) transparent transparent transparent;
  filter: brightness(0.85);
}

.sub-menu .current-post-ancestor a::after {
  display: none;
}

.menu-item.menu-item-has-children a {
  padding-right: 2.5rem;
}

.menu-item.menu-item-has-children a::before {
  content: "";
  position: absolute;
  right: 15px;
  top: 50%;
  height: 10px;
  width: 10px;
  transform: translateY(-50%);
}

.menu .menu-item .sub-menu {
  display: none;
  position: absolute;
  background: #fff;
  width: 350px;
  top: 3rem;
  padding: 1rem;
  left: -3rem;
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.15);
  counter-reset: number;
}

.menu .menu-item:hover .sub-menu {
  display: block;
}

.menu .menu-item .sub-menu .menu-item {
  margin: 0;
}

.menu .menu-item .sub-menu .menu-item a {
  color: var(--dark);
  padding: 0.5rem 1rem;
  line-height: 1.3;
  letter-spacing: 0px;
  font-size: 1rem;
  text-transform: none;
  margin-left: 2rem;
  background: transparent;
}

.menu .menu-item .sub-menu .menu-item::before {
  content: counter(number, decimal-leading-zero) " ";
  counter-increment: number;
  position: absolute;
  top: 11px;
  font-weight: bold;
  color: var(--color);
}

.menu .menu-item .sub-menu .menu-item a:hover,
.menu .menu-item .sub-menu .current-menu-item a {
  color: var(--color);
}

.sub-menu a::before {
  display: none;
}

