.mobile-icon {
  position: fixed;
  right: 3rem;
  top: 30px;
  z-index: 1000;
}

.mobile-icon .close {
  width: 0rem;
  margin-right: 50px;
}

.mobile-icon .close .close-text {
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 1.1rem;
  transition: 0.5s ease-out;
  opacity: 0;
  -webkit-transform: translateX(150%);
  -ms-transform: translateX(150%);
  transform: translateX(150%);
  text-align: right;
}

.open {
  position: relative;
  top: 0;
  right: 0;
  width: 30px;
  height: 20px;
  display: block;
  cursor: pointer;
  overflow: hidden;
  z-index: 100;
  -webkit-transition: opacity 0.2s linear;
  transition: opacity 0.2s linear;
  margin: 2px 0px 0 auto;
}

.open span {
  display: block;
  height: 2px;
  width: 20px;
  background-color: var(--dark);
  position: absolute;
  right: 0px;
  top: 0px;
  overflow: hidden;
  -webkit-transition: all 0.42s cubic-bezier(0.645, 0.045, 0.355, 1);
  -moz-transition: all 0.42s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: all 0.42s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.open span:nth-child(1) {
  margin-top: 3px;
  width: 30px;
  z-index: 9;
}

.open span:nth-child(2) {
  margin-top: 9px;
}

.open span:nth-child(3) {
  margin-top: 15px;
  width: 10px;
}

.open span:nth-child(3) {
  margin-top: 15px;
  width: 30px;
}

.open:hover span {
  width: 30px;
}

/* .  
====================
*/
.active.mobile {
  visibility: visible;
  opacity: 1;
  padding: 2rem;
  padding-top: 1rem;
  overflow-y: auto;
}

.active.mobile-icon .close {
  width: 6.25rem;
}

.active.mobile-icon .close .close-text {
  opacity: 1;
  -webkit-transform: translateX(0%);
  -ms-transform: translateX(0%);
  transform: translateX(0%);
}

.active .open span {
  width: 30px;
}

.active span:nth-child(2) {
  overflow: visible;
}

.active span:nth-child(1),
.active span:nth-child(3) {
  z-index: 100;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.active span:nth-child(1) {
  margin-top: -1px;
  -webkit-transform: rotate(-45deg) translateY(7px) translateX(-7px);
  transform: rotate(-45deg) translateY(7px) translateX(-7px);
}

.active span:nth-child(2) {
  left: -30px;
  width: 0px;
}

.active span:nth-child(3) {
  -webkit-transform: rotate(45deg) translateY(-4px) translateX(-4px);
  transform: rotate(45deg) translateY(-4px) translateX(-4px);
}

