  flex-direction: column;
  display: flex;
}
.page-404 .title > * {
  display: inline-block;
  font-size: 200px;
}
.page-404 .number {
  text-shadow: 20px 20px 20px rgba(0, 0, 0, 0.2);
  padding: 0 0.2em;
